import React from 'react'

const Menu = () => {
  const [token, setToken] = React.useState<string | null>('');

  const route = (path:string)=>{

  }
  React.useEffect(()=>{
    const savedToken = localStorage.getItem('token');
    setToken(savedToken);
  }, [])
  return (
    <div>
      <p style={styles} className='nav__right-link'>About</p>
      <p style={styles} className='nav__right-link'>Workout Videos</p>
      <p style={styles} className='nav__right-link'>Insructor Training</p>
      <p style={styles} className='nav__right-link'>Membership</p>
      <p style={styles} className='nav__right-link'>Contact Us</p>
      {token === ''  || token === null ? <p onClick={()=> route('/signup')} className='nav__right-link'>Signup / Signin</p> : <p className='nav__right-link'>Logout</p>}
    </div>
  )
}

const styles = {
  borderBottom:'1px solid black',
  fontSize:'1.5rem',
}
export default Menu
