import React from 'react'
import StripeCheckout from 'react-stripe-checkout';
import DoneIcon from '@material-ui/icons/Done';
import CloseIcon from '@material-ui/icons/Close';
import Navigation from '../Components/Navigation.component';
import '../Stylesheet/Pages/Member.scss'
import axios from 'axios';


const Member = () => {

    const subscribe = (price:string, type:string)=>{
        const base = {
            dev:'http://localhost:5000/api/subscription',
            prod:'https://hiithop.herokuapp.com/api/subscription'
        }
        const url = process.env.NODE_ENV === 'production' ? base.prod : base.dev;

        axios.get(url, {
            headers:{
                'price':price
            }
        }).then((response)=>{
            window.location.href = response.data.url
            localStorage.setItem('muxaka', type);
        }).catch((error)=>{
            alert('Network Error: Failed to create subscription please try again later')
        })
    }
    return (
        <div>
            <Navigation /> 
            <div id='membercard-container'>
                <div className='member-card'>
                    <h3>Standard</h3>
                    <h4>Standard HiitHop Membership access</h4>
                    <div>
                        <div className='card-benefits-info'>
                            <DoneIcon style={{color:'green'}}/>
                            <p>Workout Videos</p>
                        </div>
                        <div className='card-benefits-info'>
                            <CloseIcon style={{color:'red'}}/>
                            <p>Create playlist</p>
                        </div>
                    </div>
                    <button onClick={()=> subscribe('price_1JKjBuDkDtXXMeQD2cW2zC79', 'Standard')}>$10/month</button>
                </div>
                <div className='member-card'>
                    <h3>Premiumd</h3>
                    <h4>Premium HiitHop Membership access</h4>
                    <div>
                        <div className='card-benefits-info'>
                            <DoneIcon style={{color:'green'}}/>
                            <p>Workout Videos</p>
                        </div>
                        <div className='card-benefits-info'>
                            <DoneIcon style={{color:'green'}}/>
                            <p>Create playlist</p>
                        </div>
                    </div>
                    <button onClick={()=> subscribe('price_1JKjCJDkDtXXMeQD5ekU1ilI', 'Premium')}>$15/month</button>
                </div>
            </div>
        </div>
    )
}


export default Member