import axios from 'axios';
import React from 'react'
import { useHistory } from 'react-router-dom'
import Navigation from '../Components/Navigation.component'
import '../Stylesheet/Pages/Videos.scss'

export type Video = {
    name:string,
    description:string,
    video:string
}

const Workoutvideos = () => {
    const history = useHistory()
    const [token, setToken] = React.useState<String| null>('')
    const [videos, setVideos] = React.useState<Array<Video>>([])
    React.useEffect(()=>{
        
        const token = localStorage.getItem('token')
        // setToken(token)
        if (token === null){
            history.push('/signin')
            return; 
        }
        const base = {
            dev:'http://localhost:5000/api/video',
            prod:'https://hiithop.herokuapp.com/api/video'
        }
        const url = process.env.NODE_ENV === 'production' ? base.prod : base.dev;

        axios.get(url, {headers:{'token':token}}).then((response)=>{
            setVideos(response.data.videos)
            console.log(response.data)
        }).catch((error)=>{
            history.push('/membership')
        })
    }, [])
    return (
        <div>
            <Navigation />
            <div>
                {videos.length === 0 ? null :
                    <div id='video-container'>
                        {
                            videos.map((video, idx)=>{
                                return(
                                    <div className='video'>
                                        <div className='video__container'>
                                            <iframe src={video.video} width="640" height="564" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
                                        </div>
                                        <div>
                                            <h3>{video.name}</h3>
                                            <p>{video.description}</p>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                }
            </div>
        </div>
    )
}


export default Workoutvideos
