import { Facebook, Instagram, WhatsApp } from "@material-ui/icons"

const Footer = () => {
  return (
    <div>
      <h3 style={{textAlign: 'center', fontWeight:300, marginTop:'2rem'}}>Copyright © 2021 HiitHop | All Rights Reserved</h3>
      <div style={{textAlign:'center', justifyContent:'space-between', width:'100%'}}>
        <Facebook style={styles.icon}/>
        <Instagram style={styles.icon}/>
        <WhatsApp style={styles.icon}/>
      </div>
    </div>
  )
}

const styles = {
  header:{
    textAlign: 'center',
    fontWeight:300,
    marginTop:'2rem'
  },
  icons:{
    textAlign:'center',
    justifyContent:'space-between',
    width:'100%'
  },
  icon:{
    marginLeft:'20px'
  }
}


export default Footer
