import Home from "./Pages/Home";
import Signup from './Pages/Signup';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Signin from "./Pages/Signin";
import Member from "./Pages/Member";
import PaymentSuccess from "./Pages/PaymentSuccess";
import Workoutvideos from "./Pages/Workoutvideos";
import Videos from "./Pages/Videos";

function App() {
  return (
    <Router>
      <Switch>
        <Route component={Workoutvideos} path='/workout-videos' exact={true}/>
        <Route component={PaymentSuccess} path='/payment-success' exact={true}/>
        <Route component={Member} path='/membership' exact={true}/>
        <Route component={Signin} path='/signin' exact={true}/>
        <Route component={Signup} path='/signup' exact={true}/>
        <Route component={Home} path='/' exact={true}/>
      </Switch>
    </Router>
  );
}

export default App;
