import React from 'react'
import Banner from "../Components/Banner.component"
import Footer from '../Components/Footer.component'
import Navigation from "../Components/Navigation.component"
import '../Stylesheet/Pages/Home.scss'

const Home = () => {

  return (
    <div>
      <Navigation />
      <Banner />
      <main id='main'>
        <div id='main__services'>
          <div id='services__container'>
            <h3 id='services__header'>What we do</h3>
            <div id='service__info'>
              <div data-aos='fade-in'>
                <h3>Lorem ipsum</h3>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque, officia ex</p>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ipsa, illo pariatur? </p>
              </div>
              <img id='service-image' src='/service-img1.jpg' alt='HITT HOP Services'/>
            </div>
          </div>
          <div id='main__be-member'>
            <div>
              <h3>Want Perfect Body Shape</h3>
              <p id='member-secondary-text'>Hiit Hop is the training. It's high intensity interval training to music. Kind of like choreographical boot camp. It's its own style of workout</p>
            </div>
            <button id='btn-become-member'>Be A Member</button>
          </div>
        </div>
      </main>
      <Footer />
    </div>
  )
}

export default Home
