import React from 'react'
import '../Stylesheet/Components/Navigation.scss'
import Menu from '@material-ui/icons/Menu'
import Popup from 'reactjs-popup';
import MenuComponent from './Menu.component'
import { useHistory } from 'react-router-dom'
import 'reactjs-popup/dist/index.css';

const Navigation = () => {
  const history = useHistory();
  const [token, setToken] = React.useState<string | null>('');
  const route = (path:string)=>{
    history.push(path)
  }

  React.useEffect(()=>{
    const savedToken = localStorage.getItem('token');
    setToken(savedToken);
  }, [])

  const logout = ()=>{
    localStorage.removeItem('token');
    history.push('/')
    window.location.reload()
  }
  return (
    <div id='nav'>
      <div id='nav__left'>
        <img onClick={()=>route('/')} style={{cursor:'pointer'}} src='/mainlogo.jpg' id='nav__logo' alt='Hiit Hop brand logo'/>
      </div>
      <div id='nav__right'>
        <p onClick={()=> route('/about')} className='nav__right-link'>About</p>
        <p onClick={()=> route('/workout-videos')} className='nav__right-link'>Workout Videos</p>
        <p onClick={()=> route('/instructors')} className='nav__right-link'>Insructor Training</p>
        <p onClick={()=> route('/membership')} className='nav__right-link'>Membership</p>
        <p onClick={()=> route('/contact')} className='nav__right-link'>Contact Us</p>
        {token === '' || token === null ? <p onClick={()=> route('/signup')} className='nav__right-link'>Signup / Signin</p> : <p onClick={logout} className='nav__right-link'>Logout</p>}
        <Popup trigger={
          <Menu className='nav__right-menu-icon'/>
        } position='bottom right'>
          <MenuComponent />
        </Popup>
      </div>
    </div>
  )
}

export default Navigation
